.feedbackContainer {
    background: url('./images/FeedbackBG.png') no-repeat center center/cover;
    background-attachment: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 75vh;
    /* width: 100vw; */
}

#username {
    background-color: white;
}

.personicon {
    height: 50px;
    /* width: 50vw; */
    margin-top: 0%;
}

#feedbackArea {
    height: 100px;
    width: 300px;
    background-color: white;
    color: black;
    font-size: 1.1rem;
    margin-top: 20px;
    box-shadow: 10px 10px 31px -5px rgba(0,0,0,0.75);
    border-radius: 10px;
    padding: 5px;
}

.bottom {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: column;
}

#submitButton {
    margin-top: 20px;
    font-size: 1.05rem;
    font-weight: bold;
    padding: 5px 35px;
    border: 1px black solid ;
    border-radius: 10px;
    box-shadow: 10px 10px 31px -5px rgba(0,0,0,0.75);
}

.centerContainer {
    background-color: rgba(255, 255, 255, 0.541);
    margin: 50px;
    width: 50vw ;
    /* box-sizing: ; */
    padding: 50px;
    border-radius: 15px;
    box-shadow: 10px 10px 24px 0px rgba(0,0,0,0.75);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.top {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    flex-direction: row;
    
}

.heading {
    font-weight:bolder;
    margin-bottom: 10px;
    font-size: 2rem;
}

.messageShow{
    padding-top: 15px;
}