.quizContainer{
    /* background: url('./images/quizBG.svg') no-repeat center center/cover; */
    /* background-attachment: fixed; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 69.1vh;
}

.quizContainer::before {
    content: "";
    background: url('./images/quizBG.svg') no-repeat center center/cover;
    opacity: 0.79;
    z-index: -51;
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    background-attachment: fixed;
}

.displayMessage {
    font-size: 4.5rem;
}

.smallText {
    font-size: 1.5rem;
    font-weight: 600;
    margin-top: 10px;
}

p {
    margin: 0%;
}