*{
    /* color: #e7e55c; */
    outline: none;
}

.container {
    color: #e7e55c;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    height: 100vh;
}

.container::before {
    content: "";
    background: url('./images/backgroundLogin.png') no-repeat center center/cover;
    opacity: 0.89;
    z-index: -51;
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

.innerBox {
    /* border: 1px solid black; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.logo {
    width: 100px;
    margin: 50px 0px;
}

form > label, form > input {
    display: block;
    font-size: 1.05rem;
    text-align: left;
}

input {
    margin: 7px 0px 20px 0px;
    border: 0px;
    background-color: transparent;
    border-bottom: 2px solid black;
    border-right    : 2px solid black;
    border-radius: 10px;
    box-shadow: 10px 10px 31px -5px rgba(0,0,0,0.75);
    padding: 10px;
    color: white;
}

#loginButton {
    padding: 5px 50px;
    background-color: transparent;
    box-shadow: 10px 10px 31px -5px rgba(0,0,0,0.75);
    border: 1px solid black;
    font-size: 1.08rem;
    margin-top: 10px;
    margin-bottom: 20px;
    border-radius: 10px;
    color: #e7e55c;
    /* display: block; */
}

#loginButton:hover {
    background-color: #121212;
    color: white;
    transform: scale(1.04);
    cursor:pointer;
}

.terms {
    width: 230px;
    font-size: smaller;
    margin-top: 30px;
    text-align: justify;
}

.newAccount:hover {
    color: white;
    text-decoration: none;
    cursor: pointer;
}

.pwdContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 7px 0px 20px 0px;
    border-bottom: 2px solid black;
    border-right    : 2px solid black;
    border-radius: 10px;
    box-shadow: 10px 10px 31px -5px rgba(0,0,0,0.75);
}

.passwordInput {
    box-sizing: border-box;
    /* padding: 0%; */
    margin: 0%;
    width: 200px;
    border: 0px;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0);
}

.pwd-toggle {
    cursor: pointer;
    width: 25px;
    padding-right: 8px;
}

.message {
    width: 234px;
    /* margin-top: 20px; */
    color: white;
}